<script setup lang="ts">
import { onMounted, ref, computed } from 'vue';
import YandexForm from '@/components/YandexForm.vue';

// Interactive How It Works state
const activeStep = ref(0);

const setActiveStep = (step: number) => {
  activeStep.value = step;
};

const progressWidth = computed(() => {
  if (activeStep.value === 0) return 0;
  if (activeStep.value === 1) return 33;
  if (activeStep.value === 2) return 66;
  if (activeStep.value === 3) return 100;
  return 0;
});

// Auto-play animation
let autoPlayInterval: NodeJS.Timeout | null = null;

const startAutoPlay = () => {
  if (autoPlayInterval) clearInterval(autoPlayInterval);
  
  autoPlayInterval = setInterval(() => {
    activeStep.value = (activeStep.value + 1) % 4;
  }, 2000); // Change step every 2 seconds
};

const stopAutoPlay = () => {
  if (autoPlayInterval) {
    clearInterval(autoPlayInterval);
    autoPlayInterval = null;
  }
};

const scrollToFeatures = () => {
  const element = document.getElementById('features');
  if (element) {
    element.scrollIntoView({ behavior: 'smooth' });
  }
};

const scrollToContact = () => {
  const element = document.getElementById('contact');
  if (element) {
    element.scrollIntoView({ behavior: 'smooth' });
  }
};

onMounted(() => {
  // Start auto-play animation
  startAutoPlay();

  // Smooth scrolling for anchor links
  const links = document.querySelectorAll('a[href^="#"]');
  links.forEach(link => {
    link.addEventListener('click', (e) => {
      e.preventDefault();
      const targetId = link.getAttribute('href')?.substring(1);
      const targetElement = document.getElementById(targetId || '');
      if (targetElement) {
        targetElement.scrollIntoView({ behavior: 'smooth' });
      }
    });
  });

  // Intersection Observer for animations
  const observerOptions = {
    threshold: 0.1,
    rootMargin: '0px 0px -50px 0px'
  };

  const observer = new IntersectionObserver((entries) => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        entry.target.classList.add('animate-fade-in-up');
      }
    });
  }, observerOptions);

  // Observe elements for animation
  const animatedElements = document.querySelectorAll('.animate-on-scroll');
  animatedElements.forEach(el => observer.observe(el));
});
</script>

