<template>
  <div class="yandex-form-container">
    <!-- Yandex Form iframe -->
    <iframe 
      v-if="formUrl"
      :src="formUrl"
      width="100%" 
      height="600" 
      frameborder="0" 
      marginheight="0" 
      marginwidth="0"
      class="rounded-lg shadow-lg"
      @load="onFormLoad"
    >
      Загрузка формы...
    </iframe>
    
    <!-- Loading state -->
    <div v-if="isLoading" class="flex items-center justify-center h-96 bg-gray-50 rounded-lg">
      <div class="text-center">
        <div class="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
        <p class="text-gray-600">Загрузка формы...</p>
      </div>
    </div>
    
    <!-- Error state -->
    <div v-if="hasError" class="bg-red-50 border border-red-200 rounded-lg p-6">
      <div class="flex items-center mb-4">
        <span class="text-red-500 text-2xl mr-3">⚠️</span>
        <h3 class="text-lg font-semibold text-red-800">Ошибка загрузки формы</h3>
      </div>
      <p class="text-red-700 mb-4">
        К сожалению, форма обратной связи временно недоступна. 
        Пожалуйста, используйте альтернативные способы связи.
      </p>
      
      <!-- Alternative contact methods -->
      <div class="space-y-3">
        <div class="flex items-center">
          <span class="text-gray-600 w-24">Email:</span>
          <a 
            href="mailto:info@ai-learning.com" 
            class="text-blue-600 hover:text-blue-800 font-medium"
          >
            info@ai-learning.com
          </a>
        </div>
        <div class="flex items-center">
          <span class="text-gray-600 w-24">Телефон:</span>
          <a 
            href="tel:+7-800-123-45-67" 
            class="text-blue-600 hover:text-blue-800 font-medium"
          >
            +7 (800) 123-45-67
          </a>
        </div>
        <div class="flex items-center">
          <span class="text-gray-600 w-24">Telegram:</span>
          <a 
            href="https://t.me/ai_learning_support" 
            target="_blank"
            class="text-blue-600 hover:text-blue-800 font-medium"
          >
            @ai_learning_support
          </a>
        </div>
      </div>
    </div>
    
    <!-- Fallback form -->
    <div v-if="showFallbackForm" class="bg-white rounded-lg shadow-lg p-6">
      <h3 class="text-xl font-semibold text-gray-900 mb-6 text-center">
        Форма обратной связи
      </h3>
      
      <form @submit.prevent="submitFallbackForm" class="space-y-4">
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">
            Имя <span class="text-red-500">*</span>
          </label>
          <input
            v-model="formData.name"
            type="text"
            required
            class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500"
            placeholder="Ваше имя"
          />
        </div>
        
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">
            Email <span class="text-red-500">*</span>
          </label>
          <input
            v-model="formData.email"
            type="email"
            required
            class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500"
            placeholder="your@email.com"
          />
        </div>
        
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">
            Телефон
          </label>
          <input
            v-model="formData.phone"
            type="tel"
            class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500"
            placeholder="+7 (999) 123-45-67"
          />
        </div>
        
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">
            Интересующая программа
          </label>
          <select
            v-model="formData.program"
            class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500"
          >
            <option value="">Выберите программу</option>
            <option value="frontend">Frontend Development</option>
            <option value="backend">Backend Development</option>
            <option value="data-science">Data Science</option>
            <option value="ai-ml">AI & Machine Learning</option>
            <option value="mobile">Mobile Development</option>
            <option value="devops">DevOps</option>
            <option value="other">Другое</option>
          </select>
        </div>
        
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">
            Сообщение <span class="text-red-500">*</span>
          </label>
          <textarea
            v-model="formData.message"
            required
            rows="4"
            class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500"
            placeholder="Расскажите о ваших целях обучения или задайте вопрос..."
          ></textarea>
        </div>
        
        <div class="flex items-center">
          <input
            v-model="formData.consent"
            type="checkbox"
            required
            class="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
          />
          <label class="ml-2 block text-sm text-gray-700">
            Я согласен на обработку персональных данных
          </label>
        </div>
        
        <button
          type="submit"
          :disabled="isSubmitting"
          class="w-full bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 text-white py-3 px-6 rounded-lg font-semibold transition-colors"
        >
          <span v-if="isSubmitting" class="flex items-center justify-center">
            <svg class="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
              <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
              <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
            Отправляем...
          </span>
          <span v-else>Отправить сообщение</span>
        </button>
      </form>
      
      <!-- Success message -->
      <div v-if="isSubmitted" class="mt-6 p-4 bg-green-50 border border-green-200 rounded-lg">
        <div class="flex items-center">
          <span class="text-green-500 text-2xl mr-3">✅</span>
          <div>
            <h4 class="text-lg font-semibold text-green-800">Сообщение отправлено!</h4>
            <p class="text-green-700">
              Спасибо за обращение. Мы свяжемся с вами в ближайшее время.
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue';

// Props
interface Props {
  formId?: string;
  fallbackForm?: boolean;
}

const props = withDefaults(defineProps<Props>(), {
  formId: '65f8b8b8c8b8b8b8b8b8b8b8', // Default Yandex Form ID
  fallbackForm: true
});

// Reactive data
const isLoading = ref(true);
const hasError = ref(false);
const showFallbackForm = ref(false);
const isSubmitting = ref(false);
const isSubmitted = ref(false);

const formData = reactive({
  name: '',
  email: '',
  phone: '',
  program: '',
  message: '',
  consent: false
});

// Computed form URL
const formUrl = ref('');

// Methods
const onFormLoad = () => {
  isLoading.value = false;
  hasError.value = false;
};

const submitFallbackForm = async () => {
  isSubmitting.value = true;
  
  try {
    // Simulate form submission
    await new Promise(resolve => setTimeout(resolve, 2000));
    
    // In a real implementation, you would send the data to your backend
    console.log('Form data:', formData);
    
    // Show success message
    isSubmitted.value = true;
    
    // Reset form after 3 seconds
    setTimeout(() => {
      isSubmitted.value = false;
      Object.assign(formData, {
        name: '',
        email: '',
        phone: '',
        program: '',
        message: '',
        consent: false
      });
    }, 3000);
    
  } catch (error) {
    console.error('Form submission error:', error);
    alert('Произошла ошибка при отправке формы. Попробуйте еще раз.');
  } finally {
    isSubmitting.value = false;
  }
};

const initializeForm = () => {
  if (props.formId) {
    // Construct Yandex Form URL
    formUrl.value = `https://forms.yandex.ru/cloud/${props.formId}/?embedded=1`;
    
    // Set timeout to show fallback form if iframe doesn't load
    setTimeout(() => {
      if (isLoading.value) {
        hasError.value = true;
        isLoading.value = false;
        if (props.fallbackForm) {
          showFallbackForm.value = true;
        }
      }
    }, 10000); // 10 seconds timeout
  } else if (props.fallbackForm) {
    // Show fallback form directly if no Yandex Form ID provided
    isLoading.value = false;
    showFallbackForm.value = true;
  }
};

// Lifecycle
onMounted(() => {
  initializeForm();
});
</script>

<style scoped>
.yandex-form-container {
  width: 100%;
  max-width: 100%;
}

/* Custom iframe styling */
iframe {
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

/* Form styling */
form input:focus,
form textarea:focus,
form select:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

/* Animation for success message */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.bg-green-50 {
  animation: slideIn 0.3s ease-out;
}
</style>
